
























import Vue from 'vue';
import Workout from '@/models/Workout';
import v4 from 'uuid';

export default Vue.extend({
  name: 'WorkoutForm',
  props: {
    workout: {
      default: () => <Workout>{
        id: v4(),
        date: new Date()
      }
    }
  },
  methods: {
    save() {
      this.$store.commit('save', this.$props.workout);
      this.$router.push('/');
    }
  },
});
