




























import Vue from 'vue';
import { mapState } from 'vuex';
import Workout from '@/models/Workout';

export default Vue.extend({
  name: 'Home',
  computed: {
    ...mapState(['workouts'])
  },
  methods: {
    remove(workout: Workout) {
      this.$store.commit('remove', workout);
    }
  }
});
